import { Content, Container } from "./styled";

const Onas = () => (
    <Container>
        <Content>
            Firma KURIER s.c. Jankowscy istnieje od 1999r. Nasz gwn dziaalnoci
            jest wynajem autokarw i busw. W pocztkowej fazie rozwoju firma posiadaa 2
            autokary i realizowaa kilka regularnych pocze na trasie Moki  Biaystok 
            Moki. Jednak z biegiem lat, dziki dynamicznemu rozwojowi firmy dzi moemy
            zaproponowa Pastwu szeroki zakres usug turystycznych. Nasz tabor znacznie
            powikszy si i w swojej ofercie mamy autokary i busy z rn iloci miejsc
            do przewozu maych i wikszych grup. Posiadamy wszelkie niezbdne licencje i
            zezwolenia na poruszanie si po kraju i w ruchu midzynarodowym a w
            szczeglnoci zezwolenia na Niemcy. Obsugujemy biura podry, firmy, szkoy,
            imprezy okolicznociowe i inne.<br></br>
            <br></br>
            Realizujemy poczenia na regularnej linii autobusowej na trasie Biaystok 
            Moki  Biaystok przez Knyszyn.<br></br>
            <br></br>
            Zajmujemy si rwnie sprzeda biletw lotniczych, autokarowych oraz
            promowych. Sprzedajemy wycieczki krajowe i zagraniczne oraz kolonie dla dzieci
            i modziey. Przygotowujemy wycieczki dla grup zorganizowanych.<br></br>
            <br></br>
            Zapewniamy naszym klientom najwysz jako wiadczonych usug. Do
            kadego klienta podchodzimy indywidualnie starajc si dopasowa usug w
            zalenoci od potrzeb zamawiajcego.
        </Content>
    </Container>
);

export default Onas;