import { Content, Container, Segment, Wrapper, ImageWrapper, Image, Button } from "./styled";
import { DecorativeShape } from "../../decorativeShape";
import vehicles from "./images/vehicles.jpg"
import bus from "./images/bus.jpg"
import { toFleat } from "../../routes";
import scrollToTop from "../../scrollToTop";

const Oferta = () => (
    <Container>
        <Content>
            <div>
                Oferujemy Pastwu wynajem komfortowych autokarw i busw. Oferta
                skierowana jest do firm, grup zorganizowanych oraz osb
                indywidualnych. Nasza flota to nowoczesne busy i autokary
                wyposaone we wszelkie udogodnienia dla pasaerw (klimatyzacja,
                DVD, mikrofon, cafe bar, komfortowe fotele rozsuwane i odchylane).
                Naszych kierowcw charakteryzuje dua wiedza oglna, dyskrecja,
                gotowo do udzielenia pomocy oraz wysoka kultura osobista.
                Nowoczesno naszej floty oraz profesjonalizm naszych kierowcw
                sprawi e podr bdzie przyjemnoci.
            </div>
            <Segment>
                <Wrapper>
                    <h2>Dla osb indywidualnych, grup zorganizowanych oraz firm:</h2>
                    <ul>
                        <li>Przewozy wycieczkowe</li>
                        <li>Przejazdy na uroczystoci oraz spotkania rodzinne</li>
                        <li>Przejazdy pielgrzymkowe</li>
                        <li>Wyjazdy firmowe na szkolenia i imprezy integracyjne</li>
                        <li>Oferta obejmuje przewozy krajowe oraz zagraniczne</li>
                    </ul>
                </Wrapper>
                <ImageWrapper>
                    <Image src={vehicles} alt="autobusy"/>
                    <DecorativeShape  />
                    <DecorativeShape left="true" />
                </ImageWrapper>
            </Segment>
            <Segment>
                <ImageWrapper>
                    <Image src={bus} alt="autobus"/>
                    <DecorativeShape />
                    <DecorativeShape />
                </ImageWrapper>
                <Wrapper>
                    <h2>Inne usugi turystyczne:</h2>
                    <ul>
                        <li>Bilety lotnicze: LOT, KLM, Swiss itd.</li>
                        <li>Tanie linie lotnicze: Wizzair, Norwegian itd.</li>
                        <li>Bilety autokarowe: Eurolines Polska, Sindbad itd.</li>
                        <li>Bilety promowe: Polferries, Stena Lines itd.</li>
                        <li>Wczasy, wycieczki, kolonie i obozy dla dzieci i modziey</li>
                        <li>Przygotowanie i obsug wycieczek oraz imprez integracyjnych</li>
                        <li>Porednictwo wizowe</li>
                        <li>Ubezpieczenia AXA</li>
                    </ul>
                </Wrapper>
            </Segment>
            <Button to={toFleat} onClick={scrollToTop}>
                 Sprawd nasz flot
            </Button>
        </Content>
    </Container>
);

export default Oferta;